import { Component } from '@angular/core';
import { Repo } from './repo';
import { RepoService } from './repo.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  repos: Repo[] = [];
  search={reponame:"",username:""};
  title = 'app';
  ajaxState = 'init';
  firstpage=1;
  lastpage=1;
  currentpage=1;

  constructor(private repoService: RepoService) { }

  ngOnInit(): void {
    // this.repoService.getRepos()
    //     .then(repos => this.repos = repos);
  }
  gotoPage(page:number):void{

    this.currentpage=page;

    this.onSubmit();
  }

  onSubmit():void {
    var curren_obj=this;
    curren_obj.ajaxState = 'loading';
    let cachedresult=sessionStorage.getItem(curren_obj.search.username+"_"+curren_obj.search.reponame+"_"+curren_obj.currentpage);
    if(cachedresult && JSON.parse(cachedresult).expiry>=(new Date).getTime()){
      console.log("from cache");
      let data=JSON.parse(cachedresult);
      curren_obj.ajaxState='done';
      if(data['lastpage'])
      curren_obj.lastpage=data['lastpage'];
      return curren_obj.repos = data['response']['body'];
    }
    else
    {
      this.repoService.getRepos(curren_obj.search.username,curren_obj.search.reponame,curren_obj.currentpage).then(
            function(data){
              console.log("from api call");
              curren_obj.ajaxState='done';
              if(data['lastpage'])
              curren_obj.lastpage=data['lastpage'];
              curren_obj.repos = data['response']['body'];
              data['expiry']=(new Date).getTime()+(30*60*1000);
              sessionStorage.setItem(curren_obj.search.username+"_"+curren_obj.search.reponame+"_"+curren_obj.currentpage,JSON.stringify(data));
            }
          );
    }

  }

}



